import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import '../Css/Card.css';

class Card extends React.Component {
  render() {
    const { card: { artistName, collectionId, collectionName,
      collectionPrice, artworkUrl100, releaseDate, trackCount } } = this.props;
    return (
      <div className="div-card">
        <Link
          to={ `/album/${collectionId}` }
          data-testid={ `link-to-album-${collectionId}` }
          className="link"
        >
          <img className="img-card" src={ artworkUrl100 } alt="Album" />
          <h2>{collectionName}</h2>
          <div className="div-album-content">
            <h4>{`Artist Name: ${artistName}`}</h4>
            <h5>{`Release Date: ${releaseDate}`}</h5>
            <h5>{`Track Count: ${trackCount}`}</h5>
            <h5>{`Price: ${collectionPrice}`}</h5>
          </div>
        </Link>
      </div>
    );
  }
}

Card.propTypes = {
  card: PropTypes.shape({
    artistId: PropTypes.number,
    artistName: PropTypes.string,
    collectionId: PropTypes.number,
    collectionName: PropTypes.string,
    collectionPrice: PropTypes.number,
    artworkUrl100: PropTypes.string,
    releaseDate: PropTypes.string,
    trackCount: PropTypes.number,
  }).isRequired,
};
export default Card;
