import React from 'react';
import PropTypes from 'prop-types';
import Card from './Card';
import Loading from './Loading';
import '../Css/ListAlbuns.css';

class ListAlbuns extends React.Component {
  renderListAlbuns = () => {
    const { listAlbuns } = this.props;
    return (
      <div className="div-albuns">
        {listAlbuns.length <= 0
          ? (
            <span
              className="span-result-album"
            >
              Nenhum álbum foi encontrado
            </span>
          )
          : listAlbuns.map((album) => (
            <Card
              key={ album.collectionId }
              card={ album }
            />))}
      </div>
    );
  }

  render() {
    const { loadingCard } = this.props;
    return (
      loadingCard
        ? <span className="loading-albuns"><Loading /></span>
        : this.renderListAlbuns()
    );
  }
}

ListAlbuns.propTypes = {
  listAlbuns: PropTypes.arrayOf(PropTypes.object).isRequired,
  loadingCard: PropTypes.bool.isRequired,
};
export default ListAlbuns;
