import React from 'react';
import PropTypes from 'prop-types';
import { addSong, getFavoriteSongs, removeSong } from '../services/favoriteSongsAPI';
import Loading from './Loading';
import '../Css/MusicCard.css';

class MusicCard extends React.Component {
  constructor() {
    super();

    this.state = {
      loading: false,
      favoriteSong: false,
      classHeart: 'heart fa fa-heart-o',
    };

    // this.handleChange = this.handleChange.bind(this);
    this.renderAlbum = this.renderAlbum.bind(this);
    this.fetchFavorites = this.fetchFavorites.bind(this);
  }

  componentDidMount() {
    this.fetchFavorites();
  }

  handleClick = () => {
    const { favoriteSong } = this.state;
    const { music, callBack } = this.props;
    this.setState({ loading: true });
    if (!favoriteSong) {
      addSong(music)
        .then(() => {
          this.setState({
            loading: false,
            favoriteSong: true,
            classHeart: 'heart fa fa-heart',
          });
        });
    } else {
      this.setState({ loading: true });
      removeSong(music)
        .then(() => {
          this.setState({
            loading: false,
            favoriteSong: false,
            classHeart: 'heart fa fa-heart-o',
          });
        });
      callBack();
    }
  }

  // Créditos da Função fetchFavorites para Rodolfo Braga Turma 14B da Trybe
  async fetchFavorites() {
    const { music: { trackId } } = this.props;
    const resultMusics = await getFavoriteSongs();
    const verifyTrack = resultMusics.some((track) => track.trackId === trackId);
    if (verifyTrack) {
      this.setState({ classHeart: 'heart fa fa-heart', favoriteSong: true });
    }
  }

  renderAlbum() {
    const { music: { trackName, previewUrl } } = this.props;
    const { classHeart } = this.state;
    return (
      <section>
        <div className="div-music">
          <div className="div-track-name">
            <p className="track-name">{trackName}</p>
          </div>
          <div>
            <audio
              className="audio-component"
              data-testid="audio-component"
              src={ previewUrl }
              controls
            >
              <track kind="captions" />
              O seu navegador não suporta o elemento
              <code>audio</code>
              .
            </audio>
          </div>
          <div>
            <button
              type="button"
              className="btn-favorite"
              onClick={ this.handleClick }
            >
              <i
                className={ classHeart }
              />
            </button>
          </div>
        </div>

      </section>
    );
  }

  render() {
    const { loading } = this.state;
    return (
      loading
        ? <span className="loading-music-card"><Loading /></span>
        : this.renderAlbum()
    );
  }
}

MusicCard.propTypes = {
  music: PropTypes.shape({
    trackName: PropTypes.string,
    previewUrl: PropTypes.string,
    trackId: PropTypes.number,
  }).isRequired,
  callBack: PropTypes.func,
};

MusicCard.defaultProps = {
  callBack: () => {},
};
export default MusicCard;
