import React from 'react';
import Header from '../components/Header';
import Loading from '../components/Loading';
import ListAlbuns from '../components/ListAlbuns';
import searchAlbumsAPI from '../services/searchAlbumsAPI';
import '../Css/SearchStyle.css';

class Search extends React.Component {
  constructor() {
    super();

    this.state = {
      inputArtist: '',
      loading: false,
      artistName: '',
      albuns: [],
      visible: 'none',
      loadingCard: false,
    };

    this.handleChange = this.handleChange.bind(this);
    this.fetchAlbuns = this.fetchAlbuns.bind(this);
  }

  handleChange({ target }) {
    const { name, value } = target;
    this.setState({
      [name]: value,
    });
  }

  async fetchAlbuns() {
    try {
      const { inputArtist } = this.state;
      this.setState({ loading: true, loadingCard: true });
      const resultAlbuns = await searchAlbumsAPI(inputArtist);
      this.setState({
        loading: false,
        loadingCard: false,
        artistName: inputArtist,
        inputArtist: '',
        albuns: resultAlbuns,
        visible: 'initial',
      });
    } catch (e) {
      alert(`Ocorreu um problema ao tentar localizar os albuns erro: ${e}`);
      this.setState({ loading: false, loadingCard: false });
    }
  }

  render() {
    const { inputArtist, loading, artistName, albuns, visible, loadingCard } = this.state;
    const minLenghtArtist = 2;
    const verifyNameArtist = inputArtist.length < minLenghtArtist;
    return (
      <section data-testid="page-search">
        <Header />
        {loading
          ? <section className="loading-search"><Loading /></section>
          : (
            <form className="form">
              <div className="div-search">
                <span><i className="fa fa-search" /></span>
                <input
                  className="input-search"
                  name="inputArtist"
                  data-testid="search-artist-input"
                  type="text"
                  value={ inputArtist }
                  onChange={ this.handleChange }
                  placeholder="enter the artist or band name"
                />
              </div>

              <button
                className="button-search"
                data-testid="search-artist-button"
                type="button"
                disabled={ verifyNameArtist }
                onClick={ this.fetchAlbuns }
              >
                Pesquisar
              </button>
            </form>)}
        <div style={ { display: `${visible}` } }>
          <span className="span-result">{`Resultado de álbuns de: ${artistName}`}</span>
        </div>
        <ListAlbuns listAlbuns={ albuns } loadingCard={ loadingCard } />
      </section>
    );
  }
}

export default Search;
