import React from 'react';
import PropTypes from 'prop-types';
import Header from '../components/Header';
import getMusics from '../services/musicsAPI';
import MusicCard from '../components/MusicCard';
import Loading from '../components/Loading';
import '../Css/Album.css';

class Album extends React.Component {
  constructor() {
    super();

    this.state = {
      album: [],
      loading: true,
    };

    this.renderMusics = this.renderMusics.bind(this);
  }

  componentDidMount() {
    this.fetchMusics();
  }

  fetchMusics() {
    const { match: { params: { id } } } = this.props;
    this.setState({ loading: true },
      async () => {
        const requestMusics = await getMusics(id);
        this.setState({ loading: false, album: requestMusics });
      });
  }

  renderMusics() {
    const { album } = this.state;
    return (
      <section className="section-album-content">
        <div className="div-album-details">
          <img
            className="img-album"
            src={ album[0].artworkUrl100 }
            alt={ album[0].collectionName }
          />
          <p className="album-name" data-testid="album-name">{album[0].collectionName}</p>
          <p className="artist-name" data-testid="artist-name">{album[0].artistName}</p>
        </div>
        <div className="div-album-musics">
          {album.slice(1).map((track) => (
            <span className="music" key={ track.trackNumber }>
              <MusicCard
                music={ track }
              />
            </span>
          ))}
        </div>
      </section>
    );
  }

  render() {
    const { loading } = this.state;
    return (
      <div data-testid="page-album">
        <Header />
        {loading
          ? <span className="loading-album"><Loading /></span>
          : this.renderMusics()}
      </div>
    );
  }
}

Album.propTypes = {
  match: PropTypes.shape({
    params: PropTypes.shape({
      id: PropTypes.string,
    }),
  }).isRequired,
};
export default Album;
