import React from 'react';
import Header from '../components/Header';
import Loading from '../components/Loading';
import MusicCard from '../components/MusicCard';
import { getFavoriteSongs } from '../services/favoriteSongsAPI';
import '../Css/Favorites.css';

class Favorites extends React.Component {
  constructor() {
    super();

    this.state = {
      favoritesSongs: [],
      loading: true,
    };
  }

  componentDidMount() {
    getFavoriteSongs()
      .then((favorites) => this.setState({
        favoritesSongs: favorites,
        loading: false,
      }));
  }

  getMusics = async () => {
    const musics = await getFavoriteSongs();
    this.setState({
      favoritesSongs: musics,
      loading: false,
    });
  }

  render() {
    const { favoritesSongs, loading } = this.state;
    return (
      <div
        data-testid="page-favorites"
      >
        <Header />
        <span className="text-favorites-musics">Músicas Favoritas:</span>
        <div className="page-favorites">
          {!loading
            ? favoritesSongs.map((music) => (
              <div className="div-album-favorite" key={ music.trackId }>
                <img
                  className="img-favorite-album"
                  src={ music.artworkUrl100 }
                  alt="art work album"
                />
                <MusicCard
                  music={ music }
                  callBack={ this.getMusics }
                />
              </div>
            )) : <Loading />}
        </div>

      </div>
    );
  }
}

export default Favorites;
