import React from 'react';
import { Link } from 'react-router-dom';
import { getUser } from '../services/userAPI';
import Loading from './Loading';
import '../Css/HeaderStyle.css';
import ImageHeader from '../images/header-trybetunes.png';

class Header extends React.Component {
  constructor() {
    super();

    this.state = {
      name: '',
      loading: true,
    };
  }

  componentDidMount() {
    getUser().then((result) => this.setState({ name: result.name, loading: false }));
  }

  render() {
    const { name, loading } = this.state;
    return (
      <header
        data-testid="header-component"
        className="header"
      >
        <div className="user-logged">
          <img src={ ImageHeader } alt="Logo TrybyTunes" className="logo-header" />
          {loading
            ? <Loading />
            : <h2 data-testid="header-user-name">{name}</h2>}
        </div>
        <ul className="nav-header">
          <Link
            className="link-header"
            data-testid="link-to-search"
            to="/search"
          >
            <li className="nav-item">Search</li>
          </Link>
          <Link
            className="link-header"
            data-testid="link-to-favorites"
            to="/favorites"
          >
            <li className="nav-item">Favorites</li>
          </Link>
          <Link
            className="link-header"
            data-testid="link-to-profile"
            to="/profile"
          >
            <li className="nav-item">Profile</li>
          </Link>

        </ul>
      </header>
    );
  }
}

export default Header;
